.modal-markup{
  display: block;

@media screen and (min-width: 768px){
  display: flex;
  }
}
.modal_backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
  visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  &.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }
}
.modal_window{
  background-color: #ffffff;
  position: absolute;
  border-radius: 5px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 280px;
    height: 958px;
    padding: 48px 20px 40px;
    overflow: scroll;
  }
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 618px;
    height: 562px;
    padding: 42px 20px 30px;
    flex-direction:row;
  }
  @media screen and (min-width: 1024px) {
    flex-direction:row;
    width: 882px;
    height: 568px;
    padding: 50px 30px 40px;
  }
}
.movie_modal_img{
  border-radius: 5px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%), 0px 2px 1px rgb(0 0 0 / 20%);
  
@media screen and (min-width: 320px) and (max-width: 767px) {
  max-width: 240px;
  max-height: 356px;
  margin-bottom: 30px;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  max-width: 264px;
  max-height: 374px;
  margin-bottom: 146px;
}
@media screen and (min-width: 1024px) {
  max-width: 396px;
  max-height: 480px;
  margin-bottom: 40px;
  }
  }
.modal__button_close{
  box-sizing: border-box;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: rgba(0, 0, 0, 0.1);
  background: transparent;
}
.mobile_modal__button_close_simvol{
position: absolute;
left: 50%;
top: 50%;
transform: translate(-50%, -50%);
stroke: black;

  &:hover,
  &:focus {
    fill: #FF6B01;
    stroke: #FF6B01;
    cursor: pointer;
  }
}
.modal-table{
  width: auto;
  height: auto;
}
